<template>
    <div>
        <h1>首頁</h1>
        <h1 v-if='!flag'>歡迎:{{login_name}}</h1>

        <h1>djanog-jwt token 設置</h1>
        <form action="#" method="post" v-if='flag'>
            帳號: <input type="text" name='username' v-model="username">
            密碼: <input type="password" name='password' v-model="password">
            <button type="button" @click="login">登入</button>
        </form>
        <button type="button" @click="check_login">點我看 log </button>


        <h1>django-simple-jwt 設置</h1>
        <form action="#" method="post" v-if='flag'>
            帳號: <input type="text" name='username_simple' v-model="username_simple">
            密碼: <input type="password" name='password_simple' v-model="password_simple">
            <button type="button" @click="login_simple_jwt">登入</button>
        </form>
        <button type="button" @click="check_login_simple_jwt">點我看 log </button>
        <button type="button" @click="refresh_token">點我刷新token</button>
        <button type="button" @click="loginout">登出</button>
    </div>
</template>


<script>
import axios from '@/api/api.js'

    export default {
        name:'index',
        data(){
            return{
                username:'',
                password:'',
                username_simple:'',
                password_simple:'',
                flag:true,
                base_url:'http://127.0.0.1:8000/',
            }
        },
        methods:{
            
            // 後端 rest_framework_jwt 設置 token
            check_login(){
                // let token = 'JWT ' + localStorage.getItem('token');
                axios.get('http://127.0.0.1:8000/index/')
                .then(res=>{
                    this.login_name = res.data.username
                    this.flag = false
                })
                .catch(
                    err=>{
                        console.log(err)
                        this.flag = true}
                )
            },
            login(){
                let api = 'http://127.0.0.1:8000/login/';
                axios.post(api,{
                    username:this.username,
                    password:this.password,
                })
                .then(
                    res =>{
                        if(res.data.status === 200){
                            this.login_name = res.data.data.username
                            this.flag = false
                            localStorage.setItem('token',res.data.data.token);
                        }else{
                            console.log(res.data)
                        }
                    }
                )
                .catch(error=>alert(error + '請重新登入'))
            },


            //後端 rest_framework_simple_jwt 設置 token
            refresh_token(){
                fetch(
                    this.base_url + 'simple-jwt-token/refresh/',
                    {
                        method: 'POST',
                        body: JSON.stringify({refresh:localStorage.getItem('refresh_token')}),
                          headers:{
                                        'Content-Type':'application/json;charset=utf-8'
                                    },
                    })
                    .then(response=>{
                        if(response.status===401){
                            alert('token過期!')
                        }
                        return response.json()
                    })
                    .then(response=>{
                        // 刷新 token 設置存放 LocalStorge
                        localStorage.setItem('acess_token',response.access)
                        alert(`獲取到新的 token : ${response.access}`)
                    })
                    .catch(error=>{
                        console.log(error)
                    })

            },

            // 模擬用戶點擊頁面跳轉，查看 token 是否過期，如果過期刷新 token
            check_login_simple_jwt(){
                axios.get(this.base_url + 'index_2',{
                    headers:{'Authorization':'Bearer ' + localStorage.getItem('acess_token')}
                })
                .then(response=>{
                    this.login_name = response.data.user_name
                    this.flag = false
                })
                .catch(
                    error=>{
                        alert('您的 token 已經失效!')
                        console.log('token 已經失效',error.response.status)
                    }
                )
            },
            //登入獲取 token
            login_simple_jwt(){
                axios.post(this.base_url + 'login2/',{
                    username:this.username_simple,
                    password:this.password_simple,
                })
                .then(
                    res => {
                        if(res.data.status === 200){
                            alert(`acess_token: ${res.data.data.access_token} , refresh_token: ${res.data.data.refresh_token}`)
                            // 將 access_token 和 refresh_token 存在 locakStorage 中
                            localStorage.setItem('acess_token',res.data.data.access_token)
                            localStorage.setItem('refresh_token',res.data.data.refresh_token)
                            this.login_name = res.data.data.user_name
                            this.flag = false;
                        }
                    }
                    )
            },
            loginout(){

            }
        }
    }
</script>

<style>
   input{
       display: inline-block;
       margin: 5px;
   }
</style>


