// ========================================= rest_framework_simple_jwt 版本攔截器



// axios 為一種 全域的配置

// 請求攔截器
axios.interceptors.request.use(
    // config 為請求前的配置，
    config=>{
    // return config 會將 config 做的一些配置 回傳給 調用 axios 請求方法中
    // 如 :  config.headers.Authorization  等等的配置
        return config
    },
    // error 為 請求的錯誤處理
    error =>{
        return Promise.reject(error)
    }
)



// 響應攔截器
axios.interceptors.response.use(
    // response 為響應後的處理，可以先處理數據
    response=>{
        return response
    },
    // error ，可以透過後端響應後的狀態碼去做處理如下:

    // 透過一般處理
    error=>{
        // 如果 access_token(令牌)失效的話 響應狀態碼為 401。
        // 判斷如果響應狀態碼 401
        if(error.response.status === 401){
            // 先設置 refreshurl 的路由 
            const refreshurl = 'http://127.0.0.1:8000/simple-jwt-token/refresh/'
            // 判斷 錯誤的響應 error.config.url 路由 是否是 refreshurl (刷新 token) 的錯誤 401
            if(error.config.url !== refreshurl){
                // 如果不是則透過下面的 refreshToken 函數去設置重新 refresh 新的 access_token 來換發
                return  refreshToken().then(response=>{
                            localStorage.setItem('acess_token',response.data.access)
                            error.config.headers.Authorization = 'Bearer ' + response.data.access
                            return axios(error.config)
                        })
                // 這邊的 catch 是接收 如果 refresh_token 失效的 401 會在這邊被捕捉 然後 retrun 給調用 axios 方法的 catch 
                        .catch(err=>{
                            console.log('抱歉您的token 已經失效!')
                            return Promise.reject(err)
                        })
            }
        }
    }


    // ========= 透過 es2017 的 async/awit 處理
    // 設置 async / awit 做處理
    // async function(error) {
    //     const original_Request = error.config
    //     if(error.response.status === 401){
    //         // 透過 res = await 接收返回的數據
    //         const res = await refreshToken()
    //         // 根據後端的數據結構獲取 token
    //         const acess_token = res.data.access
            
    //         // 設置換發後的 acess_token
    //         localStorage.setItem('acess_token',acess_token)
    //         // 設置原請求頭 Authorization 修改 acess_token 的值
    //         original_Request.headers.Authorization = 'Bearer ' + acess_token
    //         // 重新發送一次 request
    //         return axios(original_Request)
    //     }
    //     return Promise.reject(error)
    // }
)





// 如果用戶 token 過期，刷新 token ， rest_framework_simple_jwt refreshToken 設置是 24小時
function refreshToken(){
    return axios.post('http://127.0.0.1:8000/simple-jwt-token/refresh/',{
        refresh:localStorage.getItem('refresh_token')
    })
}












export default axios